.stripe-module-wrapper {
  clear: both;
  margin: 0px auto;
  overflow: hidden; }

.stripe-module-header {
  clear: both;
  margin-bottom: 10px;
  overflow: hidden; }

.stripe-module-header img.stripe-logo {
  float: left;
  margin: 0px 70px 0px 0px; }

.stripe-module-header span.stripe-module-intro {
  color: #008CDD;
  display: block;
  float: left;
  font-size: 26px;
  text-align: center;
  line-height: 34px;
  margin: 0px 35px 0px 0px;
  padding: 0px 0px 0px 20px; }

.stripe-module-header a.stripe-module-create-btn {
  background: url("$img_dir/bg_btn-blue.png") left top no-repeat;
  color: #FFF;
  display: inline-block;
  font-size: 16px;
  font-weight: bold;
  height: 35px;
  line-height: 33px;
  margin: 16px 0px 0px 0px;
  padding: 0px 0px 0px 20px;
  text-shadow: 0px -1px 0px #03609E; }

.stripe-module-header a.stripe-module-create-btn span {
  background: url("$img_dir/bg_btn-blue.png") right bottom no-repeat;
  display: block;
  height: 35px;
  margin: 0px -20px 0px 0px;
  padding: 0px 20px 0px 0px; }

.stripe-module-wrap {
  width: 98%;
  background-color: #F7FBFF;
  border: #C3D9EF solid 1px;
  border-radius: 5px;
  clear: both;
  margin: 0px 0px 15px 0px;
  overflow: hidden;
  padding: 15px; }

.stripe-module-col1 {
  float: right;
  margin: 0px;
  overflow: hidden;
  width: 320px; }

.stripe-module-col2 {
  margin: 0px 15px 0px 0px;
  overflow: hidden; }

.stripe-module-col1inner {
  float: left;
  margin: 0px 10px 15px 10px;
  overflow: hidden;
  width: 98%;
  line-height: 22px;
  font-size: 12px; }

.stripe-module-col2inner {
  clear: both;
  margin: 0px;
  padding-top: 20px; }

.stripe-module-wrap .stripe-module-col2inner h3 {
  border-top: #C3D9EF solid 1px;
  padding-top: 20px; }

.stripe-module-wrap h3 {
  color: #008CDD;
  font-size: 18px;
  font-weight: normal;
  line-height: 22px;
  margin: 0px 0px 15px 0px;
  padding: 0px;
  text-shadow: 0px -1px 0px #DAF2FF; }

.stripe-module-wrap p, .stripe-module-wrap ul li {
  color: #535353;
  font-size: 12px;
  line-height: 22px;
  margin: 0px;
  padding: 0px;
  text-shadow: 0px 1px 0px #FFFFFF; }

.stripe-module-wrap a.stripe-module-btn {
  background: url("$img_dir/stripe-btn-arrow.png") left center no-repeat;
  color: #008CDD;
  display: inline-block;
  margin: 7px 0px;
  padding-left: 8px;
  text-decoration: none;
  text-shadow: 0px 1px 0px #FFFFFF; }

.stripe-module-wrap a.stripe-module-btn:hover {
  text-decoration: underline; }

.stripe-module-wrap ul {
  list-style: none;
  margin: 0px;
  padding: 0px; }

.stripe-module-wrap ul li {
  background: url("$img_dir/checkmark-blue.png") left 5px no-repeat;
  margin: 0px;
  padding: 0px 0px 0px 15px; }

img.stripe-cc {
  margin: 0px 40px 0px 0px;
  vertical-align: middle; }

.stripe-module-col1inner span {
  font-weight: bold; }

.floatRight {
  float: right; }

.stripe-module-wrap-video {
  background-color: #FFFFFF;
  border: #C3D9EF solid 1px;
  border-radius: 3px;
  padding: 6px;
  overflow: hidden; }

img.stripe-dashboard {
  margin: 0px; }

a.stripe-module-video-btn {
  display: inline-block;
  margin: 15px 0px 0px 0px;
  position: relative; }

img.stripe-video-btn {
  position: absolute;
  left: 119px;
  top: 80px; }

table.stripe-technical td {
  padding: 4px 0px; }

fieldset.stripe-settings {
  width: 670px;
  float: left;
  margin-right: 20px; }

table.stripe-settings label {
  float: none;
  padding-right: 0px;
  text-align: right;
  width: auto; }

table.stripe-settings {
  margin: 0px; }

table.stripe-settings td {
  border-bottom: #CCCED7 solid 1px;
  padding: 8px; }

table.stripe-settings td.td-right {
  color: #7F7F7F;
  font-size: 0.9em; }

table.stripe-settings td.td-noborder {
  border-bottom: none; }

table.stripe-settings td.save {
  text-align: center;
  padding-top: 15px; }

table.stripe-settings table.innerTable td {
  border: none;
  padding: 6px; }

table.stripe-settings table.innerTable td.vertBorder {
  border-left: #CCCED7 solid 1px; }

input.button:hover {
  cursor: pointer; }

fieldset.stripe-cc-numbers {
  width: 277px;
  float: left; }

table.stripe-cc-numbers {
  border: #CCCED7 solid 1px;
  background-color: #FFFFFF; }

table.stripe-cc-numbers th {
  background-color: #F7FBFF;
  border-bottom: #CCCED7 solid 1px;
  padding: 5px 25px 5px 8px;
  text-align: left; }

table.stripe-cc-numbers td {
  border-bottom: #CCCED7 solid 1px;
  padding: 5px 25px 5px 8px; }

table.stripe-cc-numbers td.last {
  border-bottom: none; }

table.stripe-cc-numbers td.number code {
  font-family: Courier; }

.stripe-module-wrapper table {
  color: #585A69;
  font-size: 12px;
  text-shadow: 0 1px 0 #FFFFFF; }

#content.bootstrap h3:not(.modal-title), #content.bootstrap .panel-heading {
  margin: 0px 0px 0px 0px; }

.commit_3d {
  margin: 10px 0; }

.commit_3d_15 {
  padding-left: 10px;
  width: 95% !important;
  font-size: 14px; }

.stripe-module-col1inner .connect_btn {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: auto;
  padding-top: 10px;
  padding-bottom: 10px; }

.stripe-connect {
  display: inline-block;
  margin-bottom: 1px;
  background-image: linear-gradient(#28A0E5, #015E94);
  -webkit-font-smoothing: antialiased;
  border: 0;
  padding: 1px;
  height: 30px;
  text-decoration: none;
  border-radius: 4px;
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.2);
  cursor: pointer;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none; }

.stripe-connect:hover, .stripe-connect:focus, .stripe-connect:active {
  text-decoration: none !important; }

.stripe-connect span {
  display: block;
  position: relative;
  padding: 0 12px 0 44px;
  height: 30px;
  background: #1275FF;
  background-image: linear-gradient(#7DC5EE, #008CDD 85%, #30A2E4);
  font-size: 14px;
  line-height: 30px;
  color: white;
  font-weight: bold;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.2);
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.25);
  border-radius: 3px; }

.stripe-connect span:before {
  content: '';
  display: block;
  position: absolute;
  left: 11px;
  top: 50%;
  width: 23px;
  height: 24px;
  margin-top: -12px;
  background-repeat: no-repeat;
  background-size: 23px 24px; }

.stripe-connect:active {
  background: #005D93; }

.stripe-connect:active span {
  color: #EEE;
  background: #008CDD;
  background-image: linear-gradient(#008CDD, #008CDD 85%, #239ADF);
  box-shadow: inset 0 1px 0 rgba(0, 0, 0, 0.1); }

.stripe-connect span:before, .stripe-connect.blue span:before {
  background-image: url("data:image/png;base64,"); }

.bootstrap .switch input:last-of-type:checked ~ a {
  border: solid 1px #279CBB !important;
  background-color: #2eacce !important; }

.table .uppercase {
  text-transform: uppercase; }

.stripe-connection .connect_btn {
  text-align: center;
  margin-top: 10px; }

#applepay_googlepay,
label[for="applepay_googlepay"] {
  display: inline-block;
  vertical-align: top; }

.faq-title {
  display: block;
  margin: 10px 0px;
  font-size: 16px;
  font-weight: bold; }

/* Retina support */
@media only screen and (-webkit-min-device-pixel-ratio: 1.5), only screen and (min--moz-device-pixel-ratio: 1.5), only screen and (min-device-pixel-ratio: 1.5) {
  .stripe-connect span:before, .stripe-connect.blue span:before {
    background-image: url("data:image/png;base64,"); } }

.img-example1 {
  width: 50%;
  margin: 20px 0px; }

.img-example2 {
  width: 100%;
  margin-top: 20px; }
